import { Injectable } from '@angular/core';
import { IDefinitionAdapter } from '@helix/platform/shared/api';
import { IContainerViewComponentDefinition, IViewDefinition, RX_VIEW_DEFINITION } from '@helix/platform/view/api';

@Injectable()
export class PageComponentDefinitionAdapterService
  implements IDefinitionAdapter<IContainerViewComponentDefinition, IViewDefinition> {
  adaptDefinition(componentDefinition: IContainerViewComponentDefinition): void {
    componentDefinition.layout = JSON.stringify({
      outlets: [
        {
          name: RX_VIEW_DEFINITION.defaultOutletName,
          columns: [
            {
              children: componentDefinition.componentDefinitions[0].guid
            }
          ]
        }
      ]
    });
  }
}
